.Login,.Register {
    margin-left: -20px;
    margin-right: -30px;
    min-width: 100%;
    text-align: center;
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    
    color: white;
    background: linear-gradient(79deg, #7439db, #C66FBC 48%, #F7944D);
  }
  
  .authform, .logform, .regform {
    display: flex;
    flex-direction: column;
  }
  
  @media screen and (min-width: 600px) {
    .auth-form-container {
      padding: 5rem;
      border: 1px solid white;
      border-radius: 10px;
      margin: 0.5rem;
    }
  }
  
  label {
    text-align: left;
    padding: 0.25rem 0;
  }
  
  input {
    margin: 0.5rem 0;
    padding: 1rem;
    border: none;
    border-radius: 10px;
  }
  
  button {
    border: none;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    cursor: pointer;
    color: #7439db;
  }
  
  .link {
    background: none;
    color: white;
    text-decoration: underline;
  }